.form {
  max-width: 300px;
  margin: 0 auto;
  padding-bottom: 20px;
}

.form > div {
  margin-bottom: 10px;
}

.title,
.checkboxWrapper,
.btnWrapper {
  text-align: center;
  text-transform: uppercase;
}

.checkboxWrapper {
  display: block;
  margin-bottom: 10px;
}

.checkbox,
.hint {
  display: inline-block;
}

.checkbox {
  border: 1px solid #ced4da;

  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.checkbox:focus {
  border-color: #80bdff;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.hint {
  padding-left: 10px;
}

.captchaWrapper {
  width: 100%;
}

.errorWrapper {
  composes: errorWrapper from "../common/styles/errorMessageForm.module.css";
}

.error {
  composes: error from "../common/styles/errorMessageForm.module.css";
}
